import React, {Component} from 'react';
import bike from './svg/bike.svg';
import logo from './svg/logo.svg';
import step1 from './svg/step1.svg';
import step2 from './svg/step2.svg';
import step3 from './svg/step3.svg';
import activeStep1 from './svg/activeStep1.svg';
import activeStep2 from './svg/activeStep2.svg';
import activeStep3 from './svg/activeStep3.svg';
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import faFacebookF from '@fortawesome/fontawesome-free-brands/faFacebookF';
import faTwitter from '@fortawesome/fontawesome-free-brands/faTwitter';
import faInstagram from '@fortawesome/fontawesome-free-brands/faInstagram';
import faCircle from '@fortawesome/fontawesome-free-solid/faCircle';
import faPhone from '@fortawesome/fontawesome-free-solid/faPhone';
import faUserCircle from '@fortawesome/fontawesome-free-solid/faUserCircle';
import faCaretDown from '@fortawesome/fontawesome-free-solid/faCaretDown';
import './App.css';

class App extends Component {
  render() {
    return (<div>
      <Header/>
      <Intro/>
      <Procedure/>
      <Footer />
    </div>);
  }
}

const Header = () => <div id="title">
  <img src={logo} alt="company logo" width='50' height='50' id='logo'/>
  <h2>GOOD<span id="titleHalf">DROP</span>
  </h2>
  <div id='navBar'>
    <h4 className='nav'><span><FontAwesomeIcon icon={faPhone} transform={{ rotate: 90 }} /></span> 9566123640</h4>
    <h4 className='nav'><a href='#' id='accLink'><span><FontAwesomeIcon icon={faUserCircle} /></span> Account</a></h4>
  </div>
</div>

const Intro = () => <div id="about">
  <h2>Bike Shipping Through Railways<br/>Convenient And Easy</h2>
  <img src={bike} alt="motorcycle" width='500' />
  <div id='btnDetails'>
    <button type='button' className='button' id='parcelBtn'>YOUR PARCEL DETAILS <span><FontAwesomeIcon icon={faCaretDown} /></span></button>
    <button type='button' className='button' id='bikeBtn'>YOUR BIKE DETAILS <span><FontAwesomeIcon icon={faCaretDown} /></span></button>
    <button type='button' className='button' id='priceBtn'>PRICE ESTIMATE</button>
  </div>
</div>

class Procedure extends Component {
  constructor(props) {
    super(props);
    this.state = {
      stepColor1: {color: '#34b3d3'},
      stepColor2: {color: '#000000'},
      stepColor3: {color: '#000000'},
      src1: activeStep1,
      src2: step2,
      src3: step3
    };
  }

  tick() {
    this.setState((prevState) => {
        if(prevState.src1 === activeStep1){
          return {
            src1: step1,
            src2: activeStep2,
            stepColor1: {color: '#000000'},
            stepColor2: {color: '#34b3d3'}
          };
        }
        if(prevState.src2 === activeStep2){
          return {
            src2: step2,
            src3: activeStep3,
            stepColor2: {color: '#000000'},
            stepColor3: {color: '#34b3d3'}
          };
        }
        else{
          return {
            src3: step3,
            src1: activeStep1,
            stepColor3: {color: '#000000'},
            stepColor1: {color: '#34b3d3'}
          };
        }
});
  }


  componentDidMount() {
    this.interval = setInterval(() => this.tick(), 3000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    const {
      stepColor1,
      stepColor2,
      stepColor3,
      src1,
      src2,
      src3
    } = this.state;

    return(
      <div id="procedure">
        <h2>Bike shipping is easy with us<br/>in 3 simple steps</h2>
        <div className="steps">
          <h3>ST<span className='underline'>EP </span>01</h3>
          <img src={src1} alt="procedure step one"  width='200' height='200' className='stepImg' />
          <h4 className="stepDesc" style={stepColor1}>Enter bike shipping<br />details</h4>
        </div>
        <div className="steps">
          <h3>ST<span className='underline'>EP </span>02</h3>
          <img src={src2} alt="procedure step one"  width='200' height='200' className='stepImg' />
          <h4 className="stepDesc" style={stepColor2}>Hand over the keys to<br />Gooddrop executives</h4>
        </div>
        <div className="steps">
          <h3>ST<span className='underline'>EP </span>03</h3>
          <img src={src3} alt="procedure step one"  width='200' height='200' className='stepImg' />
          <h4 className="stepDesc" style={stepColor3}>Get your bike<br />at your destination</h4>
        </div>
        <div id="btn">
          <button type='button' className='button' id='book'>BOOK NOW</button>
          <button type='button' className='button' id='contact'>CONTACT</button>
        </div>
        </div>
    );
  }
}

  const Footer = () => <div id="foot">
    <div className='footPart'>
      <h4>Company</h4>
      <ul id="linksList">
        <li><a href="#" className='footLinks'>About Us</a></li>
        <li><a href="#" className='footLinks'>Customer Reviews</a></li>
        <li><a href="#" className='footLinks'>Booking Terms</a></li>
        <li><a href="#" className='footLinks'>Terms Of Use</a></li>
        <li><a href="#" className='footLinks'>Privacy Policy</a></li>
      </ul>
    </div>
    <div className='footPart'>
      <h4>Support</h4>
      <ul id="linksList">
        <li><a href="#" className='footLinks'>How To</a></li><br />
        <li><a href="#" className='footLinks'>FAQs</a></li>
      </ul>
    </div>
    <div className='footPart'>
      <h4>Need Help</h4>
      <ul id="linksList">
        <li><a href="mailto:support@gooddrop.com" className='footLinks'>support@gooddrop.com</a></li>
        <li>Call at 9756432675</li>
      </ul>
    </div>
    <div className='footPart' id='social'>
      <a href='https://www.facebook.com/getaGoodDrop/' target='_blank' rel='noopener noreferrer' className='footLinks'><FontAwesomeIcon icon={faFacebookF} transform='shrink-4' mask={faCircle} size='2x' /></a>
      <a href='https://twitter.com/getaGoodDrop' target='_blank' rel='noopener noreferrer' className='footLinks'><FontAwesomeIcon icon={faTwitter} transform='shrink-4' mask={faCircle} size='2x' /></a>
      <a href='https://www.instagram.com' target='_blank' rel='noopener noreferrer' className='footLinks'><FontAwesomeIcon icon={faInstagram} transform='shrink-4' mask={faCircle} size='2x' /></a>
    </div>
    </div>

export default App;



